<template>

  <div v-if="login">
    <button @click="$router.push('/board')" >글목록</button>
    <button @click="$router.push(`/user/${user}`)" >{{user}} 님의 정보</button>
    <button @click ="loginOut" >로그아웃</button>
  </div>

  <button v-else @click="$router.push('/login')">로그인</button>
  
  

  <router-view @login-user="showLogin" ></router-view>
  
</template>

<script>




export default {
  name: 'App',
  data : ()=> ({
    login : false,
    user : '',
  }),
  methods: {
    showLogin : function(user) {
      this.user = user;
      this.login = true;
    },
    loginOut : function () {
      this.user = '';
      this.login = false;
      this.$router.push('/');
    }
  },
  components: {

  }
}
</script>

